@import "variables";
@import "animations";
@import "mixins";

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: $cyan;
  border-radius: 10px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  background-color: $bg-2;
  // font-family: "Roboto";
  // font-family: "Open Sans";
  font-family: "Staatliches";
  /* font-family: "Alegreya Sans"; */
  color: $default;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.container {
}

.aboutme {
  text-align: center;
  background-color: $bg-1;
  padding: 6rem 2rem;
  border-bottom: 2px solid #49515f;

  @include respond(pc-big) {
    padding: 8rem 2rem;
  }

  @include respond(phone-land) {
    padding: 4rem 2rem;
  }
}

.aboutme__header {
  font-size: 9rem;
  // letter-spacing: 0.3rem;
  animation-name: moveInLeft;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;

  @include respond(pc-big) {
    font-size: 10rem;
    // color: orange;
  }

  @include respond(pc-bigger) {
    font-size: 13rem;
    // color: red;
  }

  @include respond(pc-small) {
    // color: yellow;
  }

  @include respond(tab-land) {
    font-size: 7rem;
    letter-spacing: 0.1rem;
    // color: green;
  }

  @include respond(tab-port) {
    font-size: 7rem;
    line-height: 6.5rem;
    // color: blue;
  }

  @include respond(phone-land) {
    font-size: 6rem;
    line-height: 6rem;
    // color: pink;
  }

  @include respond(phone-port) {
    // color: purple;
  }
}

.aboutme__subhead {
  color: $cyan;
  font-size: 3rem;
  letter-spacing: 0.1rem;
  animation-name: moveInRight;
  animation-duration: 0.3s;
  animation-timing-function: ease-out;

  @include respond(pc-big) {
    font-size: 3.5rem;
  }

  @include respond(pc-bigger) {
    font-size: 4rem;
  }

  @include respond(tab-land) {
    font-size: 2.5rem;
  }

  @include respond(phone-port) {
    font-size: 2rem;
  }
}

.contact__list {
  margin: 0 auto;
  padding: 1.5rem;
  animation-name: moveInBottom;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;

  @include respond(pc-small) {
    padding: 1rem;
  }
}

.contact__item {
  margin: 1rem;
  height: 5rem;

  &:hover {
    margin: 0.5rem;

    @include respond(pc-small) {
      margin: 1rem;
    }
  }
}

.contact__link:link,
.contact__link:visited {
  display: block;
  color: #fff;
  text-decoration: none;
  border-radius: 0.3rem;
  height: 5rem;
  width: 5rem;
  position: relative;
  text-align: center;

  &:hover {
    height: 6rem;
    width: 6rem;
  }

  @include respond(pc-big) {
    height: 6.5rem;
    width: 6.5rem;

    &:hover {
      height: 7.5rem;
      width: 7.5rem;
    }
  }

  @include respond(pc-bigger) {
    height: 7rem;
    width: 7rem;

    &:hover {
      height: 8rem;
      width: 8rem;
    }
  }

  @include respond(pc-small) {
    height: 6rem;
    width: 6rem;
  }
}

.contact__link--linkedIn {
  background-color: #5680c2;
}

.contact__link--gitHub {
  background-color: $bg-3;
}

.contact__link--resume {
  background-color: #c29856;
}

.contact__link--email {
  background-color: #56c298;
}

.nav__icon {
  /* width: 4rem; */
  height: 4rem;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.tooltip {
  position: relative;
}

.tooltip .tooltiptext {
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  background-color: inherit;
  color: #fff;
  font-size: 1.7rem;
  text-align: center;
  height: 5rem;
  width: 100%;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);

  @include respond(pc-big) {
    font-size: 2.2rem;
  }

  @include respond(pc-bigger) {
    font-size: 2.4rem;
  }

  @include respond(pc-small) {
    top: 93%;
    left: 0;
    -ms-transform: translate(0, 0);
    transform: translate(0, 0);
    visibility: visible;
    height: auto;
    width: 100%;
    border-radius: 3px;
  }
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

